import { withPrefix } from 'gatsby';
import React from 'react';
import { withIntl } from '../i18n';
import PageWrapper from '../components/PageWrapper';
import Paragraphs from '../components/Paragraphs';
import { Breaker, ContentTitle, MultiBreaker, PageTitle, Wysiwyg } from '../ui/utilities';

const restoration = ({ t, ...context }) => {
  const restorationTypes = t('restoration.types');
  const restorationOthers = t('restoration.others');
  const types = `
    <ul>
      ${restorationTypes.map(text => `<li>${text}</li>`).join('')}
    </ul>
  `;
  const others = `
    <ul>
      ${restorationOthers.map(text => `<li>${text}</li>`).join('')}
    </ul>
  `;
  const slot1 = (
    <Breaker>
      <img src={withPrefix('assets/img/restoration-table.jpg')} alt='' />
    </Breaker>
  );
  const slot2 = (
    <MultiBreaker>
      <img src={withPrefix('assets/img/restoration-process.jpg')} alt='' />
      <img src={withPrefix('assets/img/restoration-improvement.jpg')} alt='' />
    </MultiBreaker>
  );
  const slot3 = (
    <Breaker>
      <img src={withPrefix('assets/img/restoration-analysis.jpg')} alt='' />
    </Breaker>
  );

  const description = t('restoration.description', { types, others });

  return (
    <PageWrapper
      title={t('restoration.title')}
      image={withPrefix('assets/img/restoration-process.jpg')}
      description={t('restoration.metas.description')}
      reader
      {...context}>
      <PageTitle>{t('restoration.title')}</PageTitle>
      <ContentTitle className='t-h1' aria-hidden='true'>
        {t('restoration.title')}
      </ContentTitle>
      <Wysiwyg>
        <Paragraphs lines={description} margin components={[ slot1, slot2, slot3 ]} />
      </Wysiwyg>
    </PageWrapper>
  );
};

export default withIntl()(restoration);
